<template>
  <div>
    <el-checkbox v-model="checked" class="pl-4 pt-3 pb-2" @change="changeCheckbox">
      <span class="text-base">是否撰写本章节</span>
    </el-checkbox>
    <form-profile ref="formProfileRef" :checked="checked"></form-profile>
  </div>
</template>

<script>
import FormProfile from "./FormProfile.vue"

export default {
  components: {
    FormProfile,
  },
  data() {
    return {
      checked: false,
    }
  },
  methods: {
    changeCheckbox(value) {
      this.$refs.formProfileRef.resetRequiredForm(value)
      this.$bus.emit('fromProfileCheckbox', value)
    },
  },
  created() {

  },
}
</script>
